import React, { Component } from 'react'
import { NavLink, withRouter } from 'react-router-dom'
import './index.less'

import logo from '../../assets/cutImg/logo.png'

/**
 * 导航栏
 */
class Nav extends Component {

  state = {
    itemList: [
      { id: 0, name: 'Home', to:"/home" },
      { id: 1, name: 'Blog', to:"/blog" },
      { id: 2, name: 'Questions', to:"/questions"}
    ],
    width:0,
    left:0
  }

  componentDidMount () {
    let activeId = this.confirmCurrentActiveId()
    console.log('当前活跃id', activeId)
    this.changeActiveItem(activeId)
  }

  confirmCurrentActiveId = () => {
    const pathname = this.props.location.pathname
    const currentActiveItem = this.state.itemList.filter(item => item.to === pathname)
    console.log('匹配到的id', currentActiveItem)
    return currentActiveItem[0].id
  }

  changeActiveItem = (id) => {
    const {itemList} = this.state
    const width = `${itemList[id].name.length}ch + 1vw`
    const left = `3vw + ${id * 6}vw${id ? ` + ${width}` : ''}`
    this.setState({width})
    this.setState({left})
  }

  render() {
    const {itemList,width,left} = this.state
    return (
      <div className="nav">
        <div className="nav-container shadow">
          {/* logo */}
          <div className="nav-container-logo">
            <div className="nav-container-logo-main">
              <span className="font-blue-color logo">大</span>
              <span className="font-blue-color logo">正</span>
            </div>
            <div className="nav-container-logo-base">
              <span className="font-blue-color base">Da-</span>
              <span className="font-main-color base">Z</span>
              <span className="font-blue-color base">hen</span>
              <span className="font-main-color base">g</span>
            </div>
          </div>
          <div className="mask"></div>
          {/* 跳转路由 */}
          <div className="nav-container-items">
            {/* 活跃背景块 */}
            <div 
              className="item-active"
              style={{
                width: `calc(${width})`,
                transform: `translateX(calc(${left}))`
              }}        
            ></div>
            {
              itemList.map(item => 
                <NavLink 

                  className="nav-container-items-item" 
                  to={item.to} 
                  activeClassName="active"
                  key={item.id}
                  onClick={() => this.changeActiveItem(item.id)}
                >{item.name}</NavLink>    
              )
            }
            {/* <NavLink className="nav-container-items-item" to="/home" activeClassName="active">Home</NavLink>
            <NavLink className="nav-container-items-item" to="/blog" activeClassName="active">Blog</NavLink>
            <NavLink className="nav-container-items-item" to="/questions" activeClassName="active">Questions</NavLink> */}
            {/* <NavLink className="nav-container-items-item" to="/hobby" activeClassName="active">Hobby</NavLink> */}
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(Nav)
