import React, { Component } from 'react'
import './index.less'

export default class Footer extends Component {
  render() {
    return (
      <div className="font-container">
        <div className="footer">
          <div className="footer-white"></div>
          <div className="footer-white-face">
            <div className="footer-white-face-eyes">
              <div className="footer-white-face-nose">
              <div className="footer-white-face-nose-light"></div>
              </div>
              <div className="footer-white-face-eye eye-left">
                <div className="footer-white-face-eye-soul">
                  <div className="footer-white-face-eye-soul-light"></div>
                </div>
                <div className="footer-white-face-eye-tear tear-left"></div>
              </div>
              <div className="footer-white-face-eye eye-right">
                <div className="footer-white-face-eye-soul">
                  <div className="footer-white-face-eye-soul-light"></div>
                </div>
                <div className="footer-white-face-eye-tear tear-right"></div>
              </div>
            </div>
          </div>
        </div>  
      </div>
    )
  }
}
